/* App.css */
.app-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.card {
  background-color: #f8f9fa;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 15px;
  width: 300px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-title {
  font-size: 18px;
  color: #333;
  margin-bottom: 10px;
}

.card-content {
  font-size: 14px;
  color: #666;
  margin-bottom: 20px;
}

.card-link {
  font-size: 14px;
  color: #007bff;
  text-decoration: none;
  margin-bottom: 10px;
}

.card-link:hover {
  text-decoration: underline;
}

.card-score {
  font-size: 14px;
  color: #888;
  text-align: right;
}

@media (max-width: 1280px) {
  .card {
    width: 90%;
  }
}
